.App {
  text-align: center;
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  color: white;
}

.App-header-top {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  display: flex;
  flex-direction: column;
  align-items: flex-start;
  padding: 2rem;
  background-color: #20232a;
  color: white;
  border-bottom: 1px solid #61dafb;
}

.App-title {
  font-size: 3.5rem;
  margin: 0;
  padding-left: 1rem;
  color: #ffa51f;
  text-shadow: 2px 2px 4px #344b8e;
}

.App-nav{
  margin-top: -30rem;
  padding-left: 4rem;
  align-self: flex-start;
}

.App-link {
  color: #61dafb;
  text-decoration: none;
  font-weight: bold;
  font-size: 1rem;
  margin-left: 1rem;
}

.App-link:hover {
  text-decoration: underline;
}